.wp-block-storepress-marquee {

	&:not(.has-child-selected) > .wp-block-storepress-marquee__item > *:not(.wp-block-group) {
		white-space: var(--white-space) !important;
	}

	&:not(.has-child-selected) > .wp-block-storepress-marquee__item > * > * {
		white-space: var(--white-space) !important;
	}

	&:not(.is-selected, .is-highlighted).has-overlay-color {
		position: relative;
		isolation: isolate;
		overflow: clip;

		&::before,
		&::after {
			pointer-events: none;
			position: absolute;
			content: "";
			z-index: 1;
			inset: 0;
		}

		&.orientation-x {
			&::before,
			&::after {
				width: 10%;
				height: 100%;
			}

			&::before {
				background: linear-gradient(270deg, transparent 0%, var(--overlay-color) 100%);
				margin-inline: 0 auto;
			}

			&::after {
				background: linear-gradient(90deg, transparent 0%, var(--overlay-color) 100%);
				margin-inline: auto 0;
			}
		}

		&.orientation-y {
			&::before,
			&::after {
				width: 100%;
				height: 10%;
			}

			&::before {
				background: linear-gradient(0deg, transparent 0%, var(--overlay-color) 100%);
				margin-block: 0 auto;
			}

			&::after {
				background: linear-gradient(180deg, transparent 0%, var(--overlay-color) 100%);
				margin-block: auto 0;
			}
		}
	}
}
